*{padding:0;margin:0;box-sizing:border-box;list-style-type:none}

body{display:flex;flex-direction:column;align-items:center}.app{border:1px solid black;margin-top:10px;padding:10px;min-width:90vw;max-width:90vw;min-height:600px;display:flex;display:flex;flex-direction:column;align-items:center;background-color:#e0e6ee}@media (min-width: 1000px){.app{min-width:1000px;max-width:1000px;min-height:600px}}.app .slicer{margin-top:40px;margin-bottom:40px;width:100%;border:5px dashed white}.app .copyright{width:100%;margin:30px;color:black;font-size:12px;display:flex;justify-content:center;align-items:center;font-family:"Ubuntu", sans-serif}

.headerImg{background-image:url(/startWarsREACT_deploy/static/media/LOGO.79977f05.svg);background-size:contain;background-repeat:no-repeat;background-position:center;width:300px;height:70px}

.loadingTable{width:100%;min-height:100px;border-top:3px solid rgba(110,108,108,0.2);background-color:white;display:flex;justify-content:center;align-items:center}.loadingTable .loadingIcon{width:70px;height:70px;background-image:url(/startWarsREACT_deploy/static/media/loading.3d287be5.png);background-size:cover;background-position:center;background-repeat:no-repeat;-webkit-animation:loadingAnimation 2s infinite;animation:loadingAnimation 2s infinite}@-webkit-keyframes loadingAnimation{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loadingAnimation{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.tableElement{width:100%;min-height:100px;border-top:3px solid rgba(110,108,108,0.2);background-color:white}.tableElement table{font-size:12px;width:100%;font-family:"Ubuntu", sans-serif}.tableElement table tr:nth-child(even){background:#ccc}.tableElement table tr th,.tableElement table tr td{text-align:center;font-weight:300;width:calc(100% / 7)}@media (max-width: 1000px){.tableElement table tr th,.tableElement table tr td{font-size:10px}}.tableElement table tr th{color:#00687f;border-bottom:1px solid rgba(110,108,108,0.2);position:relative}.tableElement table tr th button:nth-of-type(1){width:11px;height:11px;position:absolute;top:10%;right:0;background-image:url(data:image/png;base64,);background-position:center;background-size:cover;background-repeat:no-repeat}.tableElement table tr th button:nth-of-type(2){width:11px;height:11px;position:absolute;bottom:10%;right:0;background-image:url(data:image/png;base64,);background-position:center;background-size:cover;background-repeat:no-repeat}

.movieElement{width:100%;height:40px;background-color:white;display:flex;justify-content:space-between;align-items:center;padding:10px;margin-top:15px;border-radius:5%}.movieElement .movieTitle{font-family:"Ubuntu", sans-serif;color:#00687f;font-size:14px}.movieElement .wrapBtn{width:13px;height:13px;background-image:url("/startWarsREACT_deploy/static/media/ARROW OPEN.da45c4c0.svg");background-size:cover;background-position:center}.movieElement .wrapBtn:hover{cursor:pointer}

.loadingScreen{display:flex;justify-content:center;align-items:center;width:540px;height:330px;margin-top:15px}.loadingScreen .loadingIcon{width:70px;height:70px;background-image:url(/startWarsREACT_deploy/static/media/loading.3d287be5.png);background-size:cover;background-position:center;background-repeat:no-repeat;-webkit-animation:loadingAnimation 2s infinite;animation:loadingAnimation 2s infinite}@-webkit-keyframes loadingAnimation{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loadingAnimation{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.mainContent{padding-bottom:20px;margin-top:20px;width:90%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}

.movieFormContainer{width:90%;background-color:white;border-top:3px solid rgba(110,108,108,0.2);display:flex;justify-content:center;align-items:center}.movieFormContainer .movieForm{width:80%;padding:30px 50px 30px 50px;font-family:"Ubuntu", sans-serif;position:relative}.movieFormContainer .movieForm label,.movieFormContainer .movieForm input{display:block;border:none}.movieFormContainer .movieForm input{width:100%;margin-bottom:50px;font-size:14px;border-bottom:1px solid #999}@media (min-width: 1000px){.movieFormContainer .movieForm input{margin-bottom:40px;font-size:18px}}.movieFormContainer .movieForm input:focus{background-color:#fdffe2}.movieFormContainer .movieForm label{font-weight:300px;color:#999;font-size:14px;margin-bottom:10px;width:100%}.movieFormContainer .movieForm label.specific{position:relative}.movieFormContainer .movieForm label.specific .error{position:absolute;width:100%;color:red;border:1px solid red;top:50px;padding:5px}.movieFormContainer .movieForm label.specific .planetsFound{position:absolute;width:100%;top:50px;border:1px solid black;z-index:1}.movieFormContainer .movieForm label.specific .planetsFound .planet{background-color:white;color:black}.movieFormContainer .movieForm label.specific .planetsFound .planet:hover{background-color:#a8c4f7;cursor:pointer}.movieFormContainer .movieForm .planetsChoosen{margin-bottom:10px}.movieFormContainer .movieForm .planetsChoosen .planet{padding:5px 7px 5px 7px;margin:0 7px 0 7px;font-size:14px;display:inline-block;border:1px solid black;font-family:"Ubuntu", sans-serif;border-radius:15%}@media (min-width: 1000px){.movieFormContainer .movieForm .planetsChoosen .planet{padding:7px 9px 7px 9px;margin:0 10px 0 10px;font-size:16px}}.movieFormContainer .movieForm .planetsChoosen .planet *{display:inline-block;margin:0 10px 0 10px}.movieFormContainer .movieForm .planetsChoosen .planet div:hover{cursor:pointer}.movieFormContainer .movieForm button:hover{cursor:pointer}

.footer{width:90%;height:40px;display:flex;align-items:center;justify-content:space-between;font-family:"Ubuntu", sans-serif;color:#00687f;font-size:14px;background-color:white;padding:10px}.footer .wrapBtn{width:13px;height:13px;background-image:url("/startWarsREACT_deploy/static/media/ARROW OPEN.da45c4c0.svg");background-size:cover;background-position:center}.footer .wrapBtn:hover{cursor:pointer}

